import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function filingDate(_tableName: string) {
  const title = "Filing Date"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["filingDate"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The date the report was filed with the SEC.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyBalanceStatement?.data?.filingDate}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {info.row.original.financialData.companyBalanceStatement?.data?.filingDate}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { filingDate }
